<template>
    <div class="internet-list cf">
        <div class="left-wrap fl">
            <div class="chart-wrap line-chart">
                <p class='blockTitle'>媒体关注趋势</p>
                <div class="chart"><LineChart chartid= "trend1" :optionlist="linedatalist" /></div>
            </div>
            <div class="chart-wrap line-chart">
                <p class='blockTitle'>各媒体类型关注趋势</p>
                <div class="chart"><LineLeChart lechartid= "trend2" :leoptionlist="legenddatalist" /></div>
            </div>
            <div class="cf">
                <div class="chart-wrap small-chart fl">
                    <p class='blockTitle'>总体好评率</p>
                    <div class="chart"><PieChart piechartid= "evaluate1"  :pieoptionlist="piedatalist" /></div>
                </div>
                <div class="chart-wrap small-chart fr">
                    <p class='blockTitle'>区域分布</p>
                    <div class="chart"><MapChart mapid= "mapchart" :mapoptionlist="mapdatalist"/></div>
                </div>
            </div>
        </div>
        <div class="right-wrap fr">
            <div class="title-line">
                <p class='blockTitle'>相关动态</p>
            </div>
            <div class="dynamic-wrap scroll-area" id="scollcon">
                <ul class="cf">
                    <li v-for="item in dynamicList" class="fr">
                        <p class="dynamic-time fl">{{item.publishtime ? item.publishtime.substring(5) : ""}}<br><span class="dyear">{{item.publishtime ? item.publishtime.substring(0,4) : ""}}</span></p>
                        <p class="dynamic-info fr" :title="item.title">{{(item.title.length > 22) ? (item.title.substring(0,20) + '...') : item.title }}</p>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import cookie from '@/util/cookie'
    import api from '@/fetch/api'
    import LineChart from '@/components/echarts/line.vue'
    import LineLeChart from '@/components/echarts/line-legend.vue'
    import PieChart from '@/components/echarts/pie.vue'
    import MapChart from '@/components/echarts/map.vue'
    import VuePerfectScrollbar from 'vue-perfect-scrollbar'
    import Scrollbar from 'perfect-scrollbar'

    export default {
        name: 'internet',
        data () {
            return {
                cookie: '',
                linedatalist:{},
                legenddatalist:{},
                piedatalist:{},
                mapdatalist:[],
                dynamicList:[]
            }
        },
        mounted(){
            this.loadinfo()
        },
        methods: {
            loadinfo(){
                //媒体关注趋势
                api.starDetailsNetAttTrend(this.$route.params.id)
                    .then(res=>{
                        this.linedatalist =res.data[0]
                    })

                //各媒体类型关注趋势
                api.starDetailsNetAttTrendEach(this.$route.params.id)
                    .then(res=>{
                        this.legenddatalist =res.data[0]
                    })

                //总体好评率
                api.starDetailsNetPraisering(this.$route.params.id)
                    .then(res=>{
                        this.piedatalist = res.data[0]
                        this.piedatalist.opinion = ['好评', '差评']
                        this.piedatalist.opinionData = [
                            {
                                name: '好评',
                                value: this.piedatalist.praise
                            },{
                                name: '差评',
                                value: this.piedatalist.badreview
                            }
                        ]
                    })

                //区域分布
                api.starDetailsNetArealDistribution(this.$route.params.id)
                    .then(res=>{
                        this.mapdatalist = res.data
                        this.mapdatalist.forEach(function (item) {
                            item.name = item.area
                            item.value = item.count
                        })
                    })

                //相关动态
                api.starDetailsNetDynamic(this.$route.params.id)
                    .then(res=>{
                        this.dynamicList = res.data

                        let container = document.getElementById('scollcon')
                        //初始化滚动条
                        Scrollbar.initialize(container)
                    })
            }
        },
        components:{
            LineChart,
            LineLeChart,
            PieChart,
            MapChart
        },
        computed:{
            ...mapGetters([
                'user'
            ])
        },
        watch:{
            'user.useId'(val,oldval){
                if(val){
                    this.cookie =  cookie.get("useId")
                    this.loadinfo()
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .internet-list{
        width: 1200px;
        margin:0 auto;
        padding-top: 25px;
        margin-bottom: 50px;
    }
    .left-wrap{
        width: 884px;
    }
    .chart-wrap{
        width: 100%;
        padding: 30px 28px;
        background: #fff;
        border-radius: 5px;
    }
    .chart{
        width: 100%;
        height: calc(100% - 20px);
    }
    .small-chart{
        width: 434px;
        height: 399px;
    }
    .blockTitle{
        position: relative;
        text-align: left;
        height: 20px;
        line-height: 20px;
        color: #666;
        font-size: 16px;
        padding-left: 11px;
    }
    .blockTitle::before{
        position: absolute;
        content: '';
        left: 0;
        width: 8px;
        height: 100%;
        background: url('../../../static/images/titleIco.png') left no-repeat;
    }
    .line-chart{
        height: 287px;
        margin-bottom: 16px;
    }
    .right-wrap{
        box-sizing: border-box;
        width: 301px;
        height: 1004px;
        padding: 30px 10px 30px 15px;
        background: #fff;
        border-radius: 5px;
    }
    .title-line{
        width: 100%;
        padding-left: 13px;
    }
    .scroll-area {
        position: relative;
        /*margin: auto;*/
        margin-top: 10px;
        padding-top: 20px;
        padding-bottom: 30px;
        width: 100%;
        height: calc(100% - 30px);
        overflow: auto;
    }
    .dynamic-wrap{
        box-sizing: border-box;
        width: 100%;
    }
    .dynamic-wrap ul{
        position: relative;
        margin-right: 18px;
        padding-left: 36px;
        background: url('../../../static/images/spotLine2.png') left no-repeat;
        background-position-x: 21px;
        background-size: contain;
    }
    .dynamic-wrap ul::before{
        position: absolute;
        content: '';
        left: 21px;
        width: 10px;
        height: 25px;
        background: #fff;
    }
    .dynamic-wrap ul li{
        position: relative;
        width: 180px;
        height: 112px;
        border: 1px solid #bd53da;
        border-radius: 3px;
        border-image: -webkit-linear-gradient( to right ,#f784fd , #b198d4) 30 30;
        border-image: -moz-linear-gradient( to right ,#f784fd , #b198d4) 30 30;
        border-image: linear-gradient( to right ,#f784fd , #b198d4) 30 30;

        margin-bottom: 40px;
        padding: 10px;
        box-shadow: 0 0 20px 0 #d5e3f9;
    }
    .dynamic-wrap ul li::before{
        position: absolute;
        left: -8.8px;
        top: 15px;
        content: '';
        width: 15px;
        height: 15px;
        background: #fff;
        border: 1px solid #f784fd;
        border-top: 0;
        border-right: 0;
        transform: rotate(34deg) skewX(-18deg);
        -ms-transform:rotate(34deg) skewX(-18deg); /* IE 9 */
        -webkit-transform:rotate(34deg) skewX(-18deg); /* Safari and Chrome */
    }
    .dynamic-wrap ul li::after{
        position: absolute;
        left: -62px;
        top: 13px;
        content: '';
        width: 18px;
        height: 18px;
        border-radius: 100%;
        background: #8989fe;
        box-shadow: 0 0 0 9px rgba(217,220,250,.7);
    }
    .dynamic-wrap ul li:first-child{
        box-shadow: 0 5px 40px 5px #d5e3f9;
        background: #f101fe url('../../../static/images/dynamicBg.png') center no-repeat;
    }
    .dynamic-wrap ul li:first-child::before{
        position: absolute;
        left: -8.8px;
        top: 15px;
        content: '';
        width: 15px;
        height: 15px;
        background: #f101fe;
        border: 1px solid #f784fd;
        border-top: 0;
        border-right: 0;
        transform: rotate(34deg) skewX(-18deg);
        -ms-transform:rotate(34deg) skewX(-18deg); /* IE 9 */
        -webkit-transform:rotate(34deg) skewX(-18deg); /* Safari and Chrome */
    }
    .dynamic-wrap ul li:first-child::after{
        position: absolute;
        left: -78px;
        top: -3px;
        content: '';
        width: 50px;
        height: 50px;
        border-radius: 100%;
        background: url('../../../static/images/firstBg2.png') center no-repeat;
        box-shadow: none;
    }
    .dynamic-wrap ul li .dynamic-time,
    .dynamic-wrap ul li .dynamic-info
    {
        display: inline-block;
        /*display: -webkit-box;*/
        /*-webkit-box-orient: vertical;*/
        /*-webkit-line-clamp: 3;*/
        text-align: left;
        height: 45px;
        overflow: hidden;
    }
    .dynamic-time{
        margin-left: 5px;
        width: 55px;
        font-size: 20px;
        font-weight: bold;
        background-image: -webkit-gradient(linear, left 0, right 0, from(rgba(243, 49, 252, 1)), to(rgba(142, 106, 190, 1)));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: #da3fed;
    }
    .dynamic-info{
        width: 111px;
        color: #666;
        font-size: 14px;
    }
    .dyear{
        font-size: 14px;
    }
    .dynamic-wrap ul li:first-child .dynamic-time,
    .dynamic-wrap ul li:first-child .dynamic-info
    {
        color: #fff;
        -webkit-text-fill-color: #fff;
    }
</style>



