<template>
    <div class="progress">
        <div class="progressBar progressBar2"></div>
        <ul class="progressText">
            <li class="active">输入手机号</li>
            <li class="active">输入新密码</li>
            <li>重置成功</li>
        </ul>
        <div class="info">
            <ul>
                <li>
                    <span>新密码：</span>
                    <input type="password" class="infoInput" placeholder="请输入您新密码" v-model="user.newpassword"/>
                    <p class="tips" v-if="newpasflag">{{errormsg}}</p>
                </li>
                <li>
                    <span>再次输入：</span>
                    <input type="password" class="infoInput" placeholder="请再次输入您新密码" v-model="user.renewpassword"/>
                    <p class="tips" v-if="renewpasflag">{{errormsg}}</p>
                </li>
            </ul>
            <div class="next"><a href="javascript:void(0)" @click='editPwd()'>确定</a></div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import api from '@/fetch/api'
    export default{
        name: 'retrievePassword2',
        data(){
            return {
                newpasflag: false,
                renewpasflag: false,
                errormsg: '',
                user:{
                    phonenumber:'',
                    newpassword:'',
                    renewpassword:''
                }
            }
        },
        mounted () {
        },
        methods:{
            checkNewpas(){
                this.renewpasflag = false
                if(this.user.newpassword){
                    if(!(/^(\w){6,20}$/.exec(this.user.newpassword))){
                        this.newpasflag = true
                        this.errormsg = '只能输入6-20个字母、数字、下划线'
                        return false
                    } else {
                        this.newpasflag = false
                        return true
                    }
                } else {
                    this.newpasflag = true
                    this.errormsg = '请输入新密码'
                    return false
                }
            },
            checkReNewpas(){
                this.newpasflag = false
                if(this.user.renewpassword){
                    if(this.user.newpassword===this.user.renewpassword){
                        this.renewpasflag = false
                        return true
                    } else {
                        this.renewpasflag = true
                        this.errormsg = '密码不一致，请重新输入'
                        return false
                    }
                } else {
                    this.renewpasflag = true
                    this.errormsg = '请再次输入新密码'
                    return false
                }
            },
            editPwd(){
                if (this.checkNewpas() && this.checkReNewpas()) {
                    this.user.phonenumber=this.$route.params.phonenmber
                    api.resetPassword(this.user)
                        .then(res=>{
                            if (res.result==='1') {
                                this.$router.replace('/retrievePassword/retrievePassword3')
                            }
                            console.log(res)
                        })
                } else {
                    return false
                }
            }
        }
    }
</script>

<style scoped>
    div.info li{
        position: relative;
    }
    .tips{
        font-size: 12px;
        position: absolute;
        color: #f90;
        left:140px;
        top:36px;
    }
</style>





