<template>
    <div class="record-list">
        <ul>
            <li class="title">发行唱片</li>
            <li class="nodata" v-if="recordList.length == 0">暂无数据</li>
            <li v-for="item in recordList" class="cf">
                <div class="img-box fl">
                    <img :src = item.imageurl>
                </div>
                <div class="details-box fl">
                    <p class="recordName">{{item.recordname}}</p>
                    <ul class="cf">
                        <li>发行时间：{{item.publishtime}}</li>
                        <li>虾米播放量：{{item.xiamiplayamount}}</li>
                        <li>唱片公司：{{item.recordcompany}}</li>
                        <li>唱片制作人：{{item.producer}}</li>
                        <li>获奖记录：{{item.winningrecord}}</li>
                    </ul>
                </div>
                <div class="score fr">
                    <p class="scoreName">豆瓣评分</p>
                    <p class="scoreNum">{{item.doubanscore}}</p>
                    <p class="scoreStar"><star :score="item.doubanscore"></star></p>
                    <p class="scorePerson">{{item.commentcount}}人评价</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import cookie from '@/util/cookie'
    import star from '@/components/star/star.vue'
    import api from '@/fetch/api'
    export default {
        name: 'music',
        mounted () {
            this.firstload()
        },
        data () {
            return {
                cookie: '',
                scroll: '',
                pagecount: '',
                currentpage: 1,
                recordList:[]
            }
        },
        methods:{
            firstload(){
                api.starDetailsMusic({
                    "starid": this.$route.params.id,
                    "page": 1,
                    "rows": 8
                })
                    .then(res=>{
                        this.recordList =res.data
                        this.pagecount = (res.count%8==0) ? (res.count/8) : (parseInt(res.count/8) + 1)
                        if(this.pagecount > 1){
                            document.addEventListener('scroll', this.loadagain)
                        }
                    })
            },
            loadagain(){
                if(this.$route.name == 'music'){
                    let scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
                    this.scroll = (scrollTop == (document.body.scrollHeight-document.body.clientHeight))
                    if(this.scroll){
                        this.currentpage += 1
                        if(this.currentpage > this.pagecount){
                            return
                        }else {
                            api.starDetailsMusic({
                                "starid": this.$route.params.id,
                                "page": this.currentpage,
                                "rows": 8
                            })
                                .then(res=>{
                                    this.recordList = this.recordList.concat(res.data)
                                })
                        }
                    }
                }
            }
        },
        components:{
            star
        },
        computed:{
            ...mapGetters([
                'user'
            ])
        },
        watch:{
            'user.useId'(val,oldval){
                if(val){
                    this.cookie =  cookie.get("useId")
                    this.firstload()
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .record-list ul{
        width: 1200px;
        margin:0 auto;
        padding-top: 25px;
    }
    .title{
        margin-bottom: 30px;
        text-align: left;
        color: #666;
        font-size: 18px;
        padding-left: 30px;
        background: url('../../../static/images/records.png') left no-repeat;
    }
    .record-list li:not(.title){
        width: 100%;
        height: 194px;
        border-radius: 6px;
        overflow: hidden;
        background-color: #fff;
        margin-bottom: 30px;
        box-shadow: 0 2px 8px 2px #dfeafb;
    }
    .record-list li .img-box{
        width: 280px;
        height: 100%;
    }
    .record-list li .img-box img{
        width: 100%;
        height: 100%;
    }
    .record-list li .details-box{
        margin-left: 28px;
        width: 736px;
        height: 100%;
        padding: 35px 0 33px;
    }
    .record-list li .details-box .recordName{
        text-align: left;
        color: #666;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 25px;
    }
    .record-list li .details-box ul{
        width: 100%;
        padding-top: 0;
    }
    .record-list li .details-box li{
        display: inline-block;
        float: left;
        width: 50%;
        height: auto;
        text-align: left;
        color: #666;
        font-size: 14px;
        margin-bottom: 20px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        background: transparent;
        box-shadow: none;
    }
    .record-list li .score{
        width: 156px;
        height: 100%;
        padding: 35px 20px 33px;
    }
    .record-list li .score p{
        text-align: center;
    }
    .record-list li .score .scoreName{
        margin-bottom: 23px;
        color: #666;
        font-size: 14px;
    }
    .record-list li .score .scoreNum{
        margin-bottom: 15px;
        color: #e37dfe;
        height: 46px;
        font-size: 46px;
        font-weight: bold;
    }
    .record-list li .score .scoreStar{
        height: 14px;
        margin-bottom: 8px;
        /*background: url('../../../static/images/scoreStar.png') center no-repeat;*/
    }
    .record-list li .score .scorePerson{
        color: #0196f4;
        font-size: 12px;
    }
    .record-list li.nodata{
        width: 100%;
        height: 20px;
        line-height: 20px;
        text-align: center;
        color: #666;
        font-size: 18px;
        background: transparent;
        box-shadow: none;
    }
</style>



