<template>
    <div class="chartcon" :id='barchartid'></div>
</template>

<script>
    // 引入基本模板
    let echarts = require('echarts/lib/echarts');
    // 引入柱状图组件
    require('echarts/lib/chart/bar');
    require('echarts/lib/component/tooltip');
    export default {
        name: 'BarChart',
        props:['barchartid','highestratings','ageratings','maxdata'],
        data(){
            return{
                // 初始化图表配置
                opinion: ['平均收视率','最高收视率'],
                maxData: this.maxdata,
                opinionData: [{
                    value:this.ageratings,
                    itemStyle:{
                        normal:{
                            color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                                offset: 0,
                                color: '#0ec4fe'
                            }, {
                                offset: 1,
                                color: '#5e77fc'
                            }]),
                            borderColor:'transparent',
                            borderWidth:2,
                            barBorderRadius:[50,50,50,50]
                        },
                        emphasis:{

                        }
                    }
                },
                {
                    value:this.highestratings,
                    itemStyle:{
                        normal:{
                            color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                                offset: 0,
                                color: '#eea7fb'
                            }, {
                                offset: 1,
                                color: '#b853fb'
                            }]),
                            borderColor:'transparent',
                            borderWidth:2,
                            barBorderRadius:[50,50,50,50]
                        }
                    }
                }]
            }
        },
        mounted(){
//            this.$nextTick(function() {
//                this.initBar(this.barchartid)
//            })
            this.initBar(this.barchartid)
        },
        methods:{
            initBar(chartID) {
                let myChart = echarts.init(document.getElementById(chartID));
                myChart.setOption({
                    tooltip : {
                        trigger: 'item',
//                        backgroundColor:'#fff',
                        textStyle:{
                            color:'#fff'
                        },
//                        borderColor:'transparent',
                        padding:[5,15],
                        formatter:function(params){
                            var html = '<div ><p>' + params.value/100 + '</p><span></span></div>';
                            return params.value/100 + '%';
                        }

                    },
                    grid: {
                        left: 20,
                        right: 20,
                        bottom: 10,
                        top: 10
                    },
                    xAxis : [
                        {
                            type : 'value',
                            max: this.maxData,
                            axisTick: {
                                show:false
                            },
                            axisLabel:{
                               show:false
                            },
                            axisLine:{
                                show:false
                            },
                            splitLine:{
                                show: false
                            }
                        }
                    ],
                    yAxis : [
                        {
                            type : 'category',
                            axisTick: {
                               show:false
                            },
                            axisLabel:{
                                show:false
                            },
                            axisLine:{
                                show:false
                            },
                            splitLine:{
                                show: false
                            },
                            data:this.opinion
                        }
                    ],
                    color:['#21a9fb'],
                    series : [
                        {
                            name:'收视率',
                            type:'bar',
                            barWidth: 13,
                            barGap :10,
                            data:this.opinionData
                        }
                    ]
                });
                myChart.on('mouseover',function(data){
                    var tooltipColor = data.color.colorStops[0].color;
                    myChart.setOption({
                        tooltip:{
                            trigger:'item',
                            backgroundColor:'#fff',
                            borderColor:tooltipColor,
                            borderWidth:1,
                            position:'top',
                            textStyle:{
                                color:'#999'
                            },
                            padding:[0, 20],
                            formatter:function(params){
                                var html='<div style="position:relative;z-index:100;"><span style="position:absolute;bottom:-5px;left:30%;width:10px;height:10px;border:1px solid #bf5dfd;background:#fff;transform:rotate(45deg);z-index:-100;border-color:'+ tooltipColor+'"></span><p style="background:#fff;">'+params.value + '&nbsp;%</p></div>'
                                return html;
                            }
                        }

                    })
                });
                myChart.on('mouseout',function(data){
                    myChart.setOption({
                        series:{
                            itemStyle:{
                                normal:{
                                    borderColor:'transparent'
                                }
                            }
                        }
                    })
                })
            }
        }
    }
</script>

<style scoped>
    .chartcon{
        width: 100%;
        height: 100%;
    }
    .chart-tooltip{
        position:relative;
        width:70px;
        height:20px;
        background:#fff;
        color:#999;
        line-height:20px;
        font-size:14px;
        text-align:center;
    }
    .chart-tooltip span{
        position:absolute;
        bottom:0;
        left:30px;
        width:10px;
        height:10px;
        background:#fff;
        transform:rotate(90deg);
        -ms-transform:rotate(90deg); 	/* IE 9 */
        -moz-transform:rotate(90deg); 	/* Firefox */
        -webkit-transform:rotate(90deg); /* Safari 和 Chrome */
        -o-transform:rotate(90deg); 	/* Opera */
    }
</style>


