<template>
	<header>
		<div class="header-inner cf">
			<h1 class="fl">星购互动云经纪大数据平台</h1>
			<!--<div class="user-area fr cf" v-if="this.user.useId || cookie">-->
				<!--<span class="name fl">Hi，{{cookie ? cookie: this.user.useId}}</span>-->
				<!--<img :src="userimg" class="header-img fl">-->
				<!--<span class="out-btn fl" title="退出" @click="loginout"></span>-->
			<!--</div>-->
            <!--<div class="user-area fr cf" v-else>-->
                <!--<span class="login-btn fl" title="登录" @click="tologin"></span>-->
                <!--<span class="register-btn fl" title="注册" @click="toregister"></span>-->
            <!--</div>-->
		</div>
	</header>
</template>

<script>
    import {mapGetters,mapMutations} from 'vuex'
    import cookie from '@/util/cookie'
	export default {
		name: 'THeader',
        data(){
            return{
                username:'',
                cookie:'',
                flag:'',
                userimg: 'https://ss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=1088048985,1069225188&fm=58'
            }
        },
        computed:{
	      ...mapGetters([
	          'user'
	      ])
	    },
        mounted(){
            this.getcookie()
        },
        methods:{
            tologin(){
                this.$emit('tologin')
            },
            toregister(){
                this.$emit('toregister')
            },
            getcookie(){
                this.cookie= this.userId= cookie.get("useId")
            },
            loginout(){
                cookie.unset("useId")
            	this.cookie=cookie.get("useId")
                this.setuser({
                    useId:false
                })
               this.$router.push({ name: 'list'})
            },
            ...mapMutations({
	            setuser:'SET_USER'
	        })
        }
        
	}
</script>

<style scoped>
header{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
	background: url(../../../static/images/header-bg.png) no-repeat center;
    background-size: cover;
	height: 77px;
    box-shadow: 0 1px 20px 0 rgba(0,0,0,.1);
}
.header-inner{
	width: 1200px;
	margin: 0 auto;
}
h1{
	font-size: 30px;
    color: #5d53a7;
	line-height: 77px;
    font-weight: bold;
}
.user-area{
    display: inline-block;
	/*min-width: 230px;*/
}
.name,.header-img,.out-btn{
    display: inline-block;
}
.name{
    color: #5d53a7;
    font-size: 16px;
    line-height: 77px;
    font-weight: 500;
    font-family: '微软雅黑';
}
.header-img,.out-btn{
    height: 40px;
    width: 40px;
    margin-top: 18.5px;
}
.header-img{
	border-radius: 50%;
	margin-right: 27px;
	margin-left: 27px;
}
.out-btn{
    cursor: pointer;
    background: url("../../../static/images/loginOut.png") center no-repeat;
}
.out-btn:hover{
    background: url("../../../static/images/loginOut2.png") center no-repeat;
}
.login-btn,.register-btn{
    display: inline-block;
    margin-top: 18.5px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    overflow: hidden;
    cursor: pointer;
}
.login-btn{
    margin-right: 28px;
    background: url("../../../static/images/toLogin.png") center no-repeat;
}
.login-btn:hover{
    background: url("../../../static/images/toLogin2.png") center no-repeat;
}
.register-btn{
    background: url("../../../static/images/toRegister.png") center no-repeat;
}
.register-btn:hover{
    background: url("../../../static/images/toRegister2.png") center no-repeat;
}
</style>


