<template>
    <div class="progress">
        <div class="progressBar"></div>
        <ul class="progressText">
            <li class="active">输入手机号</li>
            <li>输入新密码</li>
            <li>重置成功</li>
        </ul>
        <div class="info">
            <form >
                <ul>
                    <li>
                        <span>手&nbsp;&nbsp;&nbsp;&nbsp;机&nbsp;&nbsp;&nbsp;&nbsp;号：</span>
                        <input type="text" class="infoInput" placeholder="请输入您的手机号" v-model='user.username' />
                        <p class="tips" v-if="pFlag">{{errormsg}}</p>
                    </li>
                    <li>
                        <span>图形验证码：</span>
                        <input type="text" class="infoInput infoInputYzm" v-model='user.imgcaptcha' placeholder="请输入图形验证码"/>
                        <span class="yzm iyzm"  @click="getImfCaptcha">
                            <img class="imgcaptcha" :src="captchaimg" alt="图形验证码">
                        </span>
                        <p class="tips" v-if="imgFlag">{{errormsg}}</p>
                    </li>
                    <li>
                        <span>短信验证码：</span>
                        <input type="text" class="infoInput infoInputYzm" v-model='user.captcha' placeholder="请输入短信验证码" />
                        <span class="yzm"  v-if="iscaptcha" @click="getCaptcha">获取验证码</span>
                        <span class="yzm disoprate"  v-if="!iscaptcha">重新发送（{{effectivenum}}）</span>
                        <p class="tips" v-if="cFlag">{{errormsg}}</p>
                    </li>
                </ul>
                <div class="next">
                    <a v-if="!iscaptcha" @click='next'>下一步</a>
                    <a href="javascript:void(0)" v-if="iscaptcha" class="disoprate">下一步</a>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    import baseconfig from '@/assets/config'
    import api from '@/fetch/api'
    export default {
        name: 'retrievePassword1',
        data(){
            return {
                pFlag: false,
                cFlag: false,
                imgFlag: false,
                iscaptcha: true,
                effectivenum: 120,
                captchaimg: '',
                errormsg: '',
                user:{
                    username:'',
                    imgcaptcha: '',
                    captcha:''
                }
            }
        },
        mounted () {
            this.getImfCaptcha()
        },
        methods:{
            controltips(tipflag,tipmsg){
                this.getImfCaptcha()
                this.pFlag = false
                this.imgFlag = false
                this.cFlag = false
                switch (tipflag){
                    case 'pFlag':
                        this.pFlag = true
                        break;
                    case 'imgFlag':
                        this.imgFlag = true
                        break;
                    case 'cFlag':
                        this.cFlag = true
                        break;
                    default:
                        return
                }
                this.errormsg = tipmsg
            },
            checkPhone(){ 
                if(!(/^1[34578]\d{9}$/.test(this.user.username))){
                    this.controltips('pFlag','请输入正确手机号')
                    this.getImfCaptcha()
                    return false; 
                }else{
                    this.pFlag=false
                    return true
                }
            },
            checkImgCaptcha(){
                if(this.user.imgcaptcha.length<=0 || this.user.imgcaptcha.length>4){
                    this.controltips('imgFlag','请输入正确图形验证码')
                    this.getImfCaptcha()
                    return false;
                }else{
                    this.imgFlag=false
                    return true
                }
            },
            checkCaptcha(){ 
                if(this.user.captcha.length<=0 || this.user.captcha.length>4){
                    this.controltips('cFlag','请输入正确短信验证码')
                    this.getImfCaptcha()
                    return false; 
                }else{
                    this.cFlag=false
                    return true
                }
            },
            getImfCaptcha(){
                return this.captchaimg = baseconfig.baseURL.substring(0,baseconfig.baseURL.length - 1) + "/user/imageCode?t=" + new Date()
            },
            getCaptcha() {
                if(this.checkPhone() && this.checkImgCaptcha()){
                    api.getCaptcha(this.user)
                        .then(res => {
                            if(res.result = '1'){
                                this.iscaptcha = !this.iscaptcha
                                let effectivetime = setInterval(() => {
                                    this.effectivenum -= 1
                                    if (this.effectivenum <= 0) {
                                        clearInterval(effectivetime)
                                        this.iscaptcha = !this.iscaptcha
                                        this.effectivenum = 120
                                        this.getImfCaptcha()
                                    }
                                }, 1000)
                            } else if (res.result === '1001' || res.result === '1008') {
                                this.controltips('pFlag',res.msg)
                            } else if (res.result === '1006') {
                                this.controltips('cFlag',res.msg)
                            } else if (res.result === '1009') {
                                this.controltips('imgFlag',res.msg)
                            } else{
                                alert(res.result)
                            }
                        }).catch(err => {
                        console.log(err)
                    })
                }else{
                    return false
                }
            },
            next(){
                if(this.checkPhone() && this.checkCaptcha()){
                    api.checkCaptcha(this.user)
                    .then(res=>{
                        if (res.result==='1') {
                            this.$router.replace(`/retrievePassword/retrievePassword2/` + this.user.username)
                        } else if (res.result === '1001' || res.result === '1008') {
                            this.controltips('pFlag',res.msg)
                        } else if (res.result === '1003' || res.result === '1004') {
                            this.controltips('cFlag',res.msg)
                        } else{
                            alert(res.msg)
                        }
                    }).catch(err => {
                    console.log(err)
                    })
                } else {
                    return false
                }
            }
        }
    }
</script>
<style scoped>
    li{
        position: relative;
        
    }
    .tips{
        font-size: 12px;
        position: absolute;
        color: #f90;
        left:140px;
        top:36px;
    }
    div.info li span.iyzm{
        background: #c0c0c0;
        overflow: hidden;
    }
    li .imgcaptcha{
        width: 100%;
        height: 100%;
        color: #a8a7a8;
        /*object-fit: cover;*/
        object-position: 2px 0;
        border: none;
    }
    .disoprate{
        background: #d7dbff !important;
        color: #fff;
        cursor: default;
    }
</style>



