<template>
  <div class="user-view" @click.self="hide">
      <div class="user-content">
        <h2 class="user-tab">
          <a href="javascript:void(0)" :class="{ 'active': islogin}" @click='tabfn'>登录</a>
          <a href="javascript:void(0)" :class="{ 'active': isregist}" @click='tabfn2'>注册</a>
        </h2>
        <div class="user-tab-wrap">
          <div class="item item-login current-item" v-if="islogin">
            <form onsubmit="return false">
              <ul>
                <li class="cf">
                  <span class="icon iconfont icon-shouji"></span>
                  <input type="text"  placeholder="手机号码" v-model='loginUser.username' @blur="checkPhone">
                  <p class="forgotline fl cf" v-if="telerrorflag">
                       <span class="errortip">{{errortip}}</span>
                  </p>
                </li>
                <li class="marb cf">
                    <div class="cf">
                        <span class='icon iconfont icon-mima'></span>
                        <input type="password" placeholder="密码"  v-model='loginUser.password' @blur="checkPassward">
                    </div>
                    <p class="forgotline cf">
                        <span class="errortip" v-if="paserrorflag">{{errortip}}</span>
                        <router-link class="forgot-pwd fr" to='/retrievePassword/retrievePassword1' @click.native="hide">忘记密码？</router-link>
                    </p>
                </li>
                <li class='submit-wrap'>
                  <input type="submit"  value="登录" @click="login">
                  <span class="cancel" @click.stop="hide">取消</span>
                </li>
              </ul>
            </form>
          </div>
          <div class="item item-register"  v-if="isregist">
             <form onsubmit="return false">
             <ul v-if="isRs">
                <li>
                  <span class="icon iconfont icon-shouji"></span>
                  <input type="text" placeholder="手机号码" v-model="user.username" @blur="checkPhone">
                    <p class="forgotline fl cf" v-if="telerrorflagR">
                        <span class="errortip">{{errortip}}</span>
                    </p>
                </li>
                 <li class="yanzheng-wrap">
                     <input type="text"  placeholder="输入图形验证码" class='yz-input imgcaptchainput' v-model="user.imgcaptcha" @blur="checkImgCaptcha">
                     <span class='get-code get-img-code' @click='chageimgCaptcha'>
                         <img class="imgcaptcha" :src="captchaimg" alt="图形验证码">
                     </span>
                     <p class="forgotline fl cf"  v-if="imgcaperrorflag">
                         <span class="errortip">{{errortip}}</span>
                     </p>
                 </li>
                <li class="yanzheng-wrap">
                  <span class='icon iconfont icon-yanzheng'></span>
                  <input type="text"  placeholder="验证码" class='yz-input' v-model="user.captcha" >
                  <a href="javascript:void(0)"  v-if="iscaptcha" class='get-code' @click='sendCaptcha'>获取验证码</a>
                  <a href="javascript:void(0)"  v-if="!iscaptcha" class='get-code disoprate'>重新发送（{{effectivenum}}）</a>
                  <p class="forgotline fl cf"  v-if="caperrorflag">
                       <span class="errortip">{{errortip}}</span>
                  </p>
                </li>
                <li>
                  <span class='icon iconfont icon-mima'></span>
                  <input type="password" v-model="user.password" placeholder="设置密码" @blur="checkPassward">
                    <p class="forgotline fl cf" v-if="paserrorflagR">
                        <span class="errortip">{{errortip}}</span>
                    </p>
                </li>
                 <li>
                     <span class='icon iconfont icon-mima'></span>
                     <input type="password" v-model="user.password2" placeholder="确认密码" @blur="checkRePassward">
                     <p class="forgotline fl cf" v-if="confirmpaserrorflag">
                         <span class="errortip">{{errortip}}</span>
                     </p>
                 </li>
                <li class='submit-wrap' :class="{ disoprate:iscaptcha }">
                  <input v-if="iscaptcha" class="disoprate" type="submit" name="" value="确认">
                  <input v-if="!iscaptcha" type="submit" name="" value="确认" @click="register">
                  <span class="cancel" @click.stop="hide">取消</span>
                </li>
              </ul>
              <div class='success-box' v-if="!isRs">
                    注册成功！
                    <p>{{num}}秒后自动跳转登陆页面</p>
              </div>
            </form>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
    import {mapGetters,mapMutations} from 'vuex'
    import baseconfig from '@/assets/config'
    import api from '@/fetch/api'
    import cookie from '@/util/cookie'
    export default {
         name: 'log-view',
         props:['activeflag'],
         data() {
             return {
                 telerrorflag: false,
                 paserrorflag: false,
                 telerrorflagR: false,
                 paserrorflagR: false,
                 caperrorflag: false,
                 imgcaperrorflag: false,
                 confirmpaserrorflag: false,
                 errortip:'',
                 islogin: true,
                 isregist: false,
                 iscaptcha: true,
                 isRs: true,
                 captchaimg: '',
                 num: 3,
                 effectivenum: 120,
                 loginUser: {
                     username: '',
                     password: ''
                 },
                 user: {
                     username: '',
                     password: '',
                     password2: '',
                     captcha: '',
                     imgcaptcha: ''
                 }
             }
         },
         mounted() {
            this.islogin = this.activeflag
            this.isregist = !this.activeflag
         },
         methods: {
             hide(){
                 this.$emit('hide')
             },
             tabfn() {
                 this.islogin = true
                 this.isregist = false
             },
             tabfn2() {
                 this.islogin = false
                 this.isregist = true
             },
             checkPhone(){
                 if(!(/^1[34578]\d{9}$/.test( this.islogin ? this.loginUser.username : this.user.username ))){
                     if(this.islogin){
                         this.controltips('telerrorflag','请输入正确手机号')
                     } else {
                         this.controltips('telerrorflagR','请输入正确手机号')
                     }
                     return false;
                 }else{
                     if(this.islogin){
                         this.telerrorflag = false
                     } else {
                         this.telerrorflagR = false
                     }
                     return true
                 }
             },
             checkPassward(){
                 if( this.islogin ? this.loginUser.password : this.user.password ){
                     if(!(/^(\w){6,20}$/.exec( this.islogin ? this.loginUser.password : this.user.password ))){
                         if(this.islogin){
                             this.controltips('paserrorflag','请输入正确密码')
                         } else{
                             this.controltips('paserrorflagR','只能输入6-20个字母、数字、下划线')
                         }
                         return false
                     } else {
                         if(this.islogin){
                             this.paserrorflag = false
                         } else{
                             this.paserrorflagR = false
                         }
                         return true
                     }
                 } else {
                     if(this.islogin){
                         this.controltips('paserrorflag','请输入密码')
                     } else{
                         this.controltips('paserrorflagR','请输入密码')
                     }
                     return false
                 }
             },
             checkRePassward(){
                 if(this.user.password){
                     if(this.user.password2){
                         if(this.user.password === this.user.password2){
                             this.confirmpaserrorflag = false
                             return true
                         } else {
                             this.controltips('confirmpaserrorflag','密码不一致，请重新输入')
                             return false
                         }
                     } else {
                         this.controltips('confirmpaserrorflag','请再次输入密码')
                         return false
                     }
                 } else {
                     this.controltips('paserrorflagR','请输入密码')
                     return false
                 }
             },
             checkImgCaptcha(){
                 if(this.user.imgcaptcha.length<=0 || this.user.imgcaptcha.length>4){
                     this.controltips('imgcaperrorflag','请输入正确图形验证码')
                     return false;
                 }else{
                     this.imgcaperrorflag = false
                     return true
                 }
             },
             checkCaptcha(){
                 if(this.user.captcha.length<=0 || this.user.captcha.length>4){
                     this.controltips('caperrorflag','请输入正确短信验证码')
                     return false;
                 }else{
                     this.caperrorflag = false
                     return true
                 }
             },
             controltips(tipflag,tipmsg){
                 this.telerrorflag = false
                 this.paserrorflag = false
                 this.telerrorflagR = false
                 this.paserrorflagR = false
                 this.caperrorflag = false
                 this.imgcaperrorflag = false
                 this.confirmpaserrorflag = false
                 switch (tipflag){
                     case 'telerrorflag':
                         this.telerrorflag = true
                         break;
                     case 'paserrorflag':
                         this.paserrorflag = true
                         break;
                     case 'telerrorflagR':
                         this.telerrorflagR = true
                         break;
                     case 'paserrorflagR':
                         this.paserrorflagR = true
                         break;
                     case 'caperrorflag':
                         this.caperrorflag = true
                         break;
                     case 'imgcaperrorflag':
                         this.imgcaperrorflag = true
                         break;
                     case 'confirmpaserrorflag':
                         this.confirmpaserrorflag = true
                         break;
                     default:
                         return
                 }
                 this.errortip = tipmsg
                 if(this.isregist){
                     this.chageimgCaptcha()
                 }
             },
             sendCaptcha() {
                 if(this.checkPhone() && this.checkImgCaptcha()){
                     api.SendCaptcha(this.user)
                         .then(res => {
                             if (res.result == "1") {
                                 this.iscaptcha = !this.iscaptcha
                                 let effectivetime = setInterval(() => {
                                     this.effectivenum -= 1
                                     if (this.effectivenum <= 0) {
                                         clearInterval(effectivetime)
                                         this.iscaptcha = !this.iscaptcha
                                         this.effectivenum = 120
                                         this.chageimgCaptcha()
                                     }
                                 }, 1000)
                             } else if(res.result === '1001' || res.result === '1007'){
                                 this.controltips('telerrorflagR',res.msg)
                             } else if(res.result === '1006'){
                                 this.controltips('caperrorflag',res.msg)
                             } else if(res.result === '1009'){
                                 this.controltips('imgcaperrorflag',res.msg)
                             } else {
                                 alert(res.msg)
                                 return
                             }
                         }).catch(err => {
                         console.log(err)
                     })
                 } else {
                     return false
                 }

             },
             chageimgCaptcha(){
                return this.captchaimg = baseconfig.baseURL.substring(0,baseconfig.baseURL.length - 1) + "/user/imageCode?t=" + new Date()
             },
             login() {
                 if(this.checkPhone() && this.checkPassward()){
                     api.Login(this.loginUser)
                     .then(res => {
                         if (res.result === '1') {
                             document.body.className = ''
                             this.controltips()
                             this.setuser({
                                 useId:this.loginUser.username
                             })
                             cookie.set("useId", this.loginUser.username,60*60*1000)
                             this.$emit('getcookie')
                             this.$emit('hide')
                         }
                         else if(res.result === '1001'){
                             this.controltips('telerrorflag',res.msg)
                         }
                         else if(res.result === '1002' || res.result === '1005'){
                             this.controltips('paserrorflag',res.msg)
                         }
                     })
                     .catch(err => {
                         console.log(err)
                     })
                 } else {
                     return false
                 }
             },
             register() {
                 if(this.checkPhone() && this.checkCaptcha() && this.checkPassward() && this.checkRePassward){
                     api.Regist(this.user)
                         .then(res => {
//                         alert(res.msg)
                             if (res.result == "1") {
                                 this.isRs = !this.isRs
                                 let timer = setInterval(() => {
                                     this.num -= 1
                                     if (this.num <= 0) {
                                         clearInterval(timer)
                                         this.islogin = true
                                         this.isregist = false
                                         this.isRs = !this.isRs
                                     }
                                 }, 1000)
                             }
                             else if(res.result === '1001' || res.result === '1007'){
                                 this.controltips('telerrorflagR',res.msg)
                             }
                             else if(res.result === '1002'){
                                 this.controltips('paserrorflagR',res.msg)
                             }
                             else if(res.result === '1003' || res.result === '1004'){
                                 this.controltips('caperrorflag',res.msg)
                             } else {
                                 alert(res.msg)
                             }
                         })
                         .catch(err => {
                             console.log(err)
                         })
                 } else {
                     return false
                 }
             },
             ...mapMutations({
                 setuser:'SET_USER'
             })
         },
         watch: {
             isregist(val,oldval){
                 if(val){
                     this.chageimgCaptcha()
                 }
             }
         }
        }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .user-view{
  position: absolute;
  left: 0;
  top:0;
  right:0;
  bottom: 0;
  /*bottom: -204px;*/
  background-color: rgba(0,0,0,.6);
  z-index: 3;
 }
 .user-content{
    /*width: 350px;*/
    width: 408px;
    /*height: 360px;*/
    position: absolute;
    top:50%;
    left:50%;
    margin-left: -204px;
    margin-top: -280px;
    background-color: #fff;
    border-radius: 10px;
 }
 .user-tab{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 54px;
  line-height: 54px;
  overflow: hidden;
 }
 .user-tab a{
  width: 50%;
  float: left;
  height: 54px;
  background-color: #e5e3e6;
  font-size: 18px;
  color: #666;

 }
  .user-tab a:hover,
  .user-tab a.active{
    color: #fff;
    background-color: #7679fe;
  }
  .item{
    padding: 35px;
  }
  .item li{
    height: 46px;
    border:1px solid #bdbac0;
    border-radius: 5px;
    margin-bottom: 24px;
    position: relative;
  }
 .item li .icon{
      height: 44px;
      line-height: 44px;
      margin: 0;
  }
  .item li:nth-child(1) .iconfont{
    font-size: 20px;
  }
  .item li .iconfont{
    float: left;
    font-size: 18px;
    margin: 0 8px;
  }
  .item li.marb{margin-bottom: 40px;}
  .item li input{
    background-color: transparent;
    height: 42px;
    line-height: 42px;
    /*width: 230px;*/
    width: calc( 100% - 40px);
    float: left;
    border:none;
    outline: none;
  }
 .item li:first-child input{
     width: calc( 100% - 40px);
 }
  .item li .yz-input{
    /*width: 100px;*/
    /*width: calc( 100% - 134px);*/
    width: calc( 100% - 140px);
  }
 .item li .imgcaptchainput{
     box-sizing: border-box;
     height: 44px;
     line-height: 44px;
     /*width: calc(100% - 100px);*/
     width: calc(100% - 105px);
     padding-left: 8px;
     border-top-left-radius: inherit;
     border-bottom-left-radius: inherit;
  }
 .item li .imgcaptcha{
      width: 100%;
      height: 100%;
      color: #a8a7a8;
      /*object-fit: cover;*/
      object-position: 2px 0;
     border: none;
  }
 .item li.submit-wrap{
    border:none;
    background: linear-gradient(to right, #74e0fc,#e4a8ff);
    /*height: 40px;*/
     height: 55px;
    box-sizing: border-box;
    text-align: center;
  }
  .item li.submit-wrap input{
    /*width: 100%;*/
    width: calc(100% - 2px);
    /*height: 100%;*/
    height: 53px;
    background: #7679fe;
    background: linear-gradient(to right, #708cff,#864cfc);
    box-sizing: border-box;
