 }
 .item li .forgot-pwd{
     display: inline-block;
     color: #ff0000;
     font-size: 12px;
 }
  .get-code{
    width: 100px;
    display: inline-block;
    background: #7679fe;
    background: linear-gradient(to right, #708cff,#864cfc);
    height: 100%;
