      /*background: #e5e3e6;*/
      /*color: #858585;*/
      background: #d7dbff !important;
      color: #fff;
      cursor: default !important;
  }
  .current-item{
    display: block;
  }
  .success-box{line-height: 30px;
    padding-top: 60px;
