    /*background:linear-gradient(0deg, #f7faff, #f7faff, #929deb, #929deb, #f7faff,#f7faff);*/
    background: url("../../../static/images/slide.png") center no-repeat;
  }
  .star-list li{
    float: left;
    width: 210px;
    height: 260px;
    overflow: hidden;
    position: relative;
    margin-right:37px;
    margin-bottom: 50px;
    box-shadow: 0 0 8px 2px #b1c3df;
    transition: all .5s ease-in-out;
    border-radius: 10px;
    cursor: pointer;
  }
  .star-list .marno{
    margin-right: 0;
  }
  .star-list li .details-box{
    position: absolute;
    width: 100%;
    height: 100%;
    top:100%;
    transition: all .3s;
    /*background-color: rgba(216,38,222,.8);*/
    background-color: rgba(190,76,205,.8);
  }
  .star-list li:hover{
    transform: scale(1.2);
  }
  .star-list li:hover .details-box {
    top:0;
  }
  /*.star-list li .details-box::after {*/
      /*position: absolute;*/
      /*content: '';*/
      /*top:67px;*/
      /*bottom: 0;*/
      /*left: 0;*/
      /*right: 0;*/
      /*background: rgba(158,50,183,.3);*/
      /*background: linear-gradient( rgba(172,50,190,.5), rgba(83,47,118,.5));*/
  /*}*/
  .star-list li:hover .img-box h3 {
    display: none
  }
  .star-list .img-box,
  .star-list .img-box img{
    width: 210px;
    height: 260px;
    position: relative;
  }
  .star-list .img-box h3{
    font-size: 16px;
    color: #fff;
    line-height: 46px;
    text-align: center;
    position: absolute;
    height: 46px;
    bottom: 0;
    left: 0;
    right: 0;
    /*background: rgba(216,38,222,.8);*/
    background-image: linear-gradient( to right, rgba(216,38,222,.8) 0%, rgba(105,61,158,.8) 100%);
  }
  .details-box img{
    width: 84px;
    height: 84px;
    border:2px solid #fff;
    border-radius: 50%;
    box-sizing: border-box;
    margin:25px auto 0;
    object-fit: cover;
    object-position: 0 0;
  }
  .details-box .name{
    display: block;
    font-size: 16px;
    color: #fff;
    margin-bottom: 4px;
    line-height: 24px;
    text-align: center;
  }
  .details-box .profile{
    display: inline-block;
    font-size: 14px;
    color: #eee;
    line-height: 26px;
    padding: 0 10px;
    height: 104px;
    text-overflow: ellipsis;
    width: 100%;
    box-sizing: border-box;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;
  }

</style>



