import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
require('./assets/mock')

Vue.config.productionTip = false

import 'static/styles/common.css'
import 'static/styles/iconfont.css'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



