<template>
    <div class="shows-list">
        <div class='sportline'><i class='sport'></i></div>
        <ul>
            <li v-for="(item,index) in recordList" class='cf' @click="selectStyle (item, index) " :class="{'active':item.active}" >
                <div class='posterCon fl'>
                    <div class='posterbox'>
                        <p class='showTime'>{{item.performancetime}}</p>
                        <p class='poster'>
                            <img class='poster' :src = item.imageurl  alt="演出海报">
                        </p>
                    </div>
                </div>
                <div class='showInfo fr'>
                    <p class='infoLine cf'>
                        <span class='showName fl'>{{item.performancename}}</span>
                        <span class='showPrice fr'>票价区间：{{item.farerange}}元</span>
                        <span class='showAddress fr'>地点：{{item.performancelocation}} </span>
                    </p>
                    <div class='charBox cf'>
                        <div class='leftCon fl'>
                            <p class='blockTitle'>关注趋势</p>
                            <div class='charCon'><LineChart :chartid= " 'trend' + index "  :optionlist="item.linedatalist" gridright="10%" /></div>
                        </div>
                        <div class='rightCon fl'>
                            <p class='blockTitle'>评价</p>
                            <div class='charCon'><PieChart :piechartid= " 'evaluate' + index "  :pieoptionlist="item.piedatalist" /></div>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import Vue from 'vue'
    import cookie from '@/util/cookie'
    import {mapGetters} from 'vuex'
    import LineChart from '@/components/echarts/line.vue'
    import PieChart from '@/components/echarts/pie.vue'
    import api from '@/fetch/api'

    export default {
        name: 'shows',
        mounted () {
            this.firstload()
        },
        data () {
            return {
                cookie: '',
                scroll: '',
                pagecount: '',
                currentpage: 1,
                recordList:[]
            }
        },
        components: {
            LineChart,
            PieChart
        },
        computed:{
            ...mapGetters([
                'user'
            ])
        },
        watch:{
            'user.useId'(val,oldval){
                if(val){
                    this.cookie =  cookie.get("useId")
                    this.firstload()
                }
            }
        },
        methods:{
            firstload(){
                //演出列表
                api.starDetailsShows({
                     "starid": this.$route.params.id,
                     "page": 1,
                     "rows": 8
                 })
                .then(res=>{
                    this.recordList=res.data
                    //console.log(this.recordList)
                    if(this.recordList && this.recordList.length != 0){
                        this.recordList.forEach(function(item1){
                            item1.linedatalist = {
                                opinion:[],
                                opinionData:[]
                            }
                            item1.piedatalist = {
                                opinion:['精彩','一般','失望'],
                                opinionData:[
                                    {
                                        name: '精彩',
                                        value: item1.performancepraisering[0].praise
                                    },{
                                        name: '一般',
                                        value: item1.performancepraisering[0].general
                                    },{
                                         name: '失望',
                                         value: item1.performancepraisering[0].badreview
                                     }
                                ]
                            }
                            item1.performanceattentiontrend.forEach(function(item2){
                                item1.linedatalist.opinion.push(item2.publishtime)
                                item1.linedatalist.opinionData.push({
                                    name: item2.publishtime,
                                    value: item2.count
                                })
                            })
                        })
                    }

                    this.pagecount = (res.count%8==0) ? (res.count/8) : (parseInt(res.count/8) + 1)
                    if(this.pagecount > 1){
                        document.addEventListener('scroll', this.loadagain)
                    }
                })
            },
            selectStyle (item, index) {
                this.recordList.forEach(function (item) {
                    Vue.set(item,'active',false);
                });
                Vue.set(item,'active',true);
            },
            loadagain(){
                if(this.$route.name == 'shows'){
                    let scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
                    this.scroll = (scrollTop == (document.body.scrollHeight-document.body.clientHeight))
                    if(this.scroll){
                        this.currentpage += 1
                        if(this.currentpage > this.pagecount){
                            return
                        }else {
                            api.starDetailsShows({
                                "starid": this.$route.params.id,
                                "page": this.currentpage,
                                "rows": 8
                            })
                            .then(res=>{
                                if(res.data && res.data != 0){
                                    res.data.forEach(function(item3){
                                        item3.linedatalist = {
                                            opinion:[],
                                            opinionData:[]
                                        }
                                        item3.piedatalist = {
                                            opinion:['精彩','一般','失望'],
                                            opinionData:[
                                                {
                                                    name: '精彩',
                                                    value: item3.performancepraisering[0].praise
                                                },{
                                                    name: '一般',
                                                    value: item3.performancepraisering[0].general
                                                },{
                                                     name: '失望',
                                                     value: item3.performancepraisering[0].badreview
                                                 }
                                            ]
                                        }
                                        item3.performanceattentiontrend.forEach(function(item4){
                                            item3.linedatalist.opinion.push(item4.publishtime)
                                            item3.linedatalist.opinionData.push({
                                                name: item4.publishtime,
                                                value: item4.count
                                            })
                                        })
                                    })
                                }
                                this.recordList = this.recordList.concat(res.data)
                            })
                        }
                    }
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .shows-list{
        position: relative;
        width: 1200px;
        margin:0 auto;
        margin-top: 50px;
    }
    .sportline{
        position: absolute;
        left: 249px;
        width: 6px;
        height: 100%;
        background: -webkit-linear-gradient(#6e43fc,#8b9aee); /* Safari 5.1-6.0 */
        background: -o-linear-gradient(#6e43fc,#8b9aee); /* Opera 11.1-12.0 */
        background: -moz-linear-gradient(#6e43fc,#8b9aee); /* Firefox 3.6-15 */
        background: linear-gradient(#6e43fc,#8b9aee); /* 标准语法 */
    }
    .sport{
        display: inline-block;
        width: 100%;
        height: 100%;
        background: url('../../../static/images/spot.png') center;
    }
    .shows-list ul{
        width: 100%;
    }
    .shows-list ul li{
        box-sizing: border-box;
        width: 100%;
        height: 335px;
    }
    .shows-list ul li .posterCon{
        box-sizing: border-box;
        width: 255px;
        height: 100%;
    }
    .shows-list ul li .posterbox{
        box-sizing: border-box;
        position: relative;
        width: 190px;
        height: 260px;
        border: 2px solid #e096f9;
        background: #fff;
        border-radius: 5px;
        padding: 15px 20px 0 20px;
        cursor: pointer;
    }
    .shows-list ul li:first-child .posterbox{
        box-shadow: 0 0 30px 3px #d4e2f9;
    }
    .shows-list ul li.active .posterbox{
        box-shadow: 0 0 30px 3px #d4e2f9;
    }
    .shows-list ul li .posterbox::before{
        position: absolute;
        right: -10px;
        top: 15px;
        content: '';
        width: 15px;
        height: 15px;
        background: #fff;
        border: 2px solid #e096f9;
        border-bottom: 0;
        border-left: 0;
        transform: rotate(31deg) skewX(-20deg);
        -ms-transform:rotate(31deg) skewX(-20deg); /* IE 9 */
        -webkit-transform:rotate(31deg) skewX(-20deg); /* Safari and Chrome */
    }
    .shows-list ul li .posterbox::after{
        position: absolute;
        right: -73px;
        top: 13px;
        content: '';
        width: 18px;
        height: 18px;
        border-radius: 100%;
        background: #8989fe;
        box-shadow: 0 0 0 9px rgba(217,220,250,.7);
        cursor: pointer;
    }
    .shows-list ul li:first-child .posterbox::after{
        position: absolute;
        right: -89px;
        top: -3px;
        content: '';
        width: 50px;
        height: 50px;
        border-radius: 100%;
        background: #f2f7ff url('../../../static/images/firstBg.png') center no-repeat;
        box-shadow: none;
    }
    .shows-list ul li.active .posterbox::after{
        position: absolute;
        right: -89px;
        top: -3px;
        content: '';
        width: 50px;
        height: 50px;
        border-radius: 100%;
        background: #f2f7ff url('../../../static/images/firstBg.png') center no-repeat;
        box-shadow: none;
    }
    .shows-list ul li .posterbox .showTime{
        width: 100%;
        text-align: center;
        color: #9f65ff;
        background-image: -webkit-gradient(linear, 0 top, 0 bottom, from(#bc5dfd), to(#7f6fff));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 36px;
        font-weight: bold;
        margin-bottom: 13px;
    }
    .shows-list ul li .posterbox .poster{
        text-align: center;
        width:100%;
        height: 180px;
        border: 1px solid #e096f9;
        border-radius: 5px;
        overflow: hidden;
    }
    .shows-list li .posterbox .poster img{
        width: 100%;
        height: 100%;
        border: none;
    }
    .shows-list ul li .showInfo{
        overflow: hidden;
        width: 880px;
        height: 295px;
        background: #fff;
        border-radius: 5px;
        box-shadow: 0 0 10px 2px rgba(223,234,251,.2);
    }
    .shows-list ul li:first-child .showInfo{
        box-shadow: 0 5px 40px 5px #d5e3f9;
    }
    .shows-list ul li .showInfo .infoLine{
        width: 100%;
        height: 48px;
        padding: 17px 26px 0;
        border-bottom: 1px solid #dbdbdb;
        cursor: pointer;
    }
    .shows-list ul li:first-child .showInfo .infoLine{
        background: #7728bb url('../../../static/images/firstLineBg.png') center no-repeat;
    }
    .shows-list ul li.active .showInfo .infoLine{
        background: #7728bb url('../../../static/images/firstLineBg.png') center no-repeat;
    }
    .shows-list ul li .infoLine span{
        display: inline-block;
        color: #666;
        font-size: 14px;
    }
    .shows-list ul li:first-child  .infoLine span{
        display: inline-block;
        color: #fff;
    }
    .shows-list ul li.active  .infoLine span{
        display: inline-block;
        color: #fff;
    }
    .shows-list ul li .infoLine span.showName{
        font-size: 16px;
        font-weight: 400;
    }
    .shows-list ul li .infoLine span.showAddress{
        margin-right: 25px;
    }
    .shows-list ul li .showInfo .charBox{
        width: 100%;
        height: calc(100% - 49px);
        padding: 30px 28px;
    }
    .shows-list ul li .charBox .leftCon{
        height: 100%;
        width: 55%;
    }
    .shows-list ul li .charBox .rightCon{
        height: 100%;
        width: 45%;
    }
    .blockTitle{
        position: relative;
        text-align: left;
        height: 20px;
        line-height: 20px;
        color: #666;
        font-size: 16px;
        padding-left: 11px;
    }
    .blockTitle::before{
         position: absolute;
         content: '';
         left: 0;
         width: 8px;
         height: 100%;
         background: url('../../../static/images/titleIco.png') left no-repeat;
    }
    .shows-list ul li .charBox .charCon{
         width: 100%;
         height: calc(100% - 20px);
    }
</style>



