<template>
  <div id="app" class='app'>
    <login-view v-if="loginflag" @hide="tohide" :activeflag="islogin"></login-view>
    <THeader @tologin="showlogin" @toregister="showregister"></THeader>
    <router-view class="router-content"></router-view>
    <Footbar></Footbar>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import THeader from '@/components/Header/index.vue'
import Footbar from '@/components/Footer/index.vue'
import loginView from '@/components/loginView/index.vue'
export default {
  name: 'app',
  data(){
      return {
          loginflag: false,
          islogin: true
      }
  },
  components: {
        THeader,
        Footbar,
        loginView
  },
  methods:{
        tohide(){
            this.loginflag = false
            document.body.className = ''
        },
       showlogin(){
           this.loginflag = true
           this.islogin = true
           document.documentElement.scrollTop = 0
//           window.pageYOffset = 0
           document.body.scrollTop = 0
           document.body.className = 'dislogin'
       },
       showregister(){
           this.loginflag = true
           this.islogin = false
           document.documentElement.scrollTop = 0
//           window.pageYOffset = 0
           document.body.scrollTop = 0
           document.body.className = 'dislogin'
       }
  },
    computed:{
        ...mapGetters([
            'user'
        ])
    },
    watch:{
        'user.useId'(val,oldval){
            if(val){
                if(this.$route.name === 'retrievePassword1' || this.$route.name === 'retrievePassword2' || this.$route.name === 'retrievePassword3'){
                    this.$router.replace({ name:'list' })
                }
            }
        }
    }
}
</script>

<style>
.app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding-top: 77px;
}
.router-content{

}
</style>



