<template>
    <div class="tologin">
        <p class="ico"></p>
        <p class="label">登录后查看更多信息</p>
        <button class="btn" @click="login">立即登录</button>
    </div>
</template>

<script>
    export default {
        name:'tologin',
        methods:{
            login(){
                this.$emit( 'showlogin')
            }
        }
    };
</script>

<style scoped>
    .tologin{
        width: 100%;
        height: 100%;
        text-align: center;
        padding-top: 30px;
    }
    .ico{
        width: 100%;
        height: 100px;
        margin-bottom: 29px;
        background: url("../../../static/images/loginTipIco.png") center no-repeat;
    }
    .label{
        text-align: center;
        font-size: 20px;
        color: #a0a2a6;
    }
    .btn{
        box-sizing: border-box;
        cursor: pointer;
        margin: 60px 0;
        width: 260px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #fff;
        font-size: 20px;
        background: #845dfb;
        background: -webkit-linear-gradient(left, #708bff , #854cfd); /* Safari 5.1 to 6.0 */
        background: -o-linear-gradient(right, #708bff , #854cfd); /* Opera 11.1 to 12.0 */
        background: -moz-linear-gradient(right, #708bff , #854cfd); /* Firefox 3.6 to 15 */
        background: linear-gradient(to right, #708bff , #854cfd); /* 标准语法 */
        border: 1px solid #6c99fe;
        border-radius: 25px;
        outline: none;
        box-shadow: 0 1px 10px 0 #dbbfea;
    }
</style>



