<template>
    <div class="movies-list">
        <div class="tv-charts-wrapper">
            <p class="title">参演电视剧排行榜</p>
            <div class="charts-box cf">
                <div class="left-charts-wrapper fl">
                    <div class="left-charts-con">
                        <div class="titleline cf">
                            <p class="chart-title fl">收视率排行</p>
                            <span class="left-legend right-legend fr"><i></i>平均收视率</span>
                            <span class="left-legend fr"><i></i>最高收视率</span>
                        </div>
                        <ul class="bottom-left-charts">
                            <!--<li class="nodata" v-if="start.length==0">暂无数据</li>-->
                            <li class="cf" v-for="(item, index) in start" :class="{active:index === 0}">
                                <span class="charts-img-wrapper fl"><img :src="item.imageurl" /></span>
                                <div class="chart fl"><BarChart :barchartid="'audiencerating'+index" :highestratings="item.highestratings" :ageratings="item.ageratings" :maxdata="100"></BarChart></div>
                                <div class="charts-charts fr">
                                    <p class="charts-num cf"><span>{{item.rank}}</span></p>
                                    <p class="charts-name">{{item.tvseriesname}}</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="left-charts-wrapper fr">
                    <div class="left-charts-con">
                        <div class="titleline"><p class="chart-title ">在线播放总量</p></div>
                        <ul class="bottom-left-charts bottom-left-charts1">
                            <!--<li class="nodata" v-if="movieOnline.length==0">暂无数据</li>-->
                            <li class="cf" v-for="(item, index) in movieOnline"  :class="{active:index === 0}">
                                <div class="tvname">{{item.tvseriesname}}</div>
                                <div class="chart1 fl"><BarOnlinePlay :baronlineplayid="'tvonlineplay'+index" :onlineplayback="item.onlineplayback" :tvseriesname="item.tvseriesname" :maxdata="1100"></BarOnlinePlay></div>
                                <div class="chart-charts1 fr">
                                    <p class="charts-num fr"><span class="left-top-box-office">{{item.rank}}</span></p>
                                    <p class="charts-name fr">{{item.onlineplayback}}万次</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div class="tv-charts-wrapper movie-charts-wrapper">
            <p class="title title1">参演电影排行榜</p>
            <div class="charts-box cf">
                <div class="left-charts-wrapper fl">
                    <div class="left-charts-con">
                        <div class="titleline cf">
                            <p class="chart-title fl">票房排行</p>
                            <span class="left-legend right-legend fr"><i></i>海外票房</span>
                            <span class="left-legend fr"><i></i>国内票房</span>
                        </div>
                        <ul class="bottom-left-charts bottom-left-charts2">
                            <!--<li class="nodata" v-if="movieBoxOffice.length==0">暂无数据</li>-->
                            <li class="cf" v-for="(item, index) in movieBoxOffice"  :class="{active:index === 0}">
                                <span class="charts-img-wrapper fl"><img :src="item.imageurl" /></span>
                                <div class="box-office fl">
                                    <p class="top-box-office"><span class="left-top-box-office fl">{{index+1}}</span>{{item.moviename}}</p>
                                    <div class="bottom-box-office">
                                        <div class="chart3 fl"><BarBoxOffice :barboxofficeid="'barboxoffice'+index" :domesticboxoffice="item.domesticboxoffice" :foreignboxoffice="(item.foreignboxoffice>0)? item.foreignboxoffice+1000 : item.foreignboxoffice" :maxdata="15000"></BarBoxOffice></div>
                                        <p class="charts-name fr">总计：<span>{{item.domesticboxoffice + item.foreignboxoffice}}</span>万元</p>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>

                </div>
                <div class="left-charts-wrapper fr">
                    <div class="left-charts-con">
                        <div class="titleline cf">
                            <p class="chart-title fl">在线播放总量</p>
                        </div>
                        <ul class="bottom-left-charts bottom-left-charts2" >
                            <!--<li class="nodata" v-if="movieOnlineMovie.length==0">暂无数据</li>-->
                            <li class="cf" v-for="(item, index) in movieOnlineMovie"  :class="{active:index === 0}">
                                <div class="box-office box-office1 fl">
                                    <p class="top-box-office"><span class="left-top-box-office fl">{{index+1}}</span>{{item.moviename}}</p>
                                    <div class="bottom-box-office">
                                        <div class="chart3 fl"><BarOnlinePlay :baronlineplayid="'meonlineplay'+index" :onlineplayback="item.onlineplayback" :maxdata="1000"></BarOnlinePlay></div>
                                        <p class="charts-name fr">{{item.onlineplayback}}万次</p>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div class="tv-charts-wrapper movie-charts-wrapper">
            <p class="title title2">影视作品豆瓣评分</p>
            <div class="charts-box cf">
                <div class="movie-list-wrapper">
                    <!--<p class="nomovie" v-if="movieList.length==0">暂无数据</p>-->
                    <ul class="movie-list-con cf">
                        <li v-for="(item,index) in movieList">
                            <img :src="item.imageurl" />
                            <p class="movie-name">{{item.name}}</p>
                            <div class="movie-score cf">
                                <span class="movie-score-num fl">{{item.doubanscore}}</span>
                                <div class="movie-score-con fr">
                                    <div class="movie-score-star"><star :score="item.doubanscore"></star></div>
                                    <p class="movie-score-people"><span>{{item.comments}}</span>评价</p>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="movie-list-btn" id="movieListBtn">
                    <span v-for="(index, item) in itemClass" @click="movieListBtn(index)" :class="{active:index === 1}"></span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import BarChart from '@/components/echarts/bar-audience-rating.vue'
    import BarOnlinePlay from '@/components/echarts/bar-online-play.vue'
    import BarBoxOffice from '@/components/echarts/bar-box-office.vue'
    import star from '@/components/star/star.vue'
    import api from '@/fetch/api'
    import cookie from '@/util/cookie'

    export default {
        name: 'movies',
        data () {
            return {
                cookie: '',
                movieList:'',
                start:'',
                movieOnline:'',
                movieBoxOffice:'',
                movieOnlineMovie:'',
                movielistlen:'',
                page:1,
                rows:6
            }
        },
        mounted () {
            this.loadmovieinfo()
        },
        methods:{
//            getcookie(data){
//                this.userId = cookie.get("useId")
//            },
            loadmovieinfo(){
                //电视剧收视率排行
                api.starDetailsMovieTv(this.$route.params.id)
                    .then(res=>{
                        if(res.data.length != 0){
                            let maxData = res.data[0].highestratings;
                            res.data.forEach(function (e) {
                                e.maxdata = maxData
                            });
                            this.start=res.data;
                        }
                    });
                //电视剧在线播放总量
                api.starDetailsMovieOnline(this.$route.params.id)
                    .then(res=>{
                        if(res.data.length != 0){
                            let maxData = res.data[0].onlineplayback;
                            res.data.forEach(function (e) {
                                e.maxdata = maxData
                            });
                            this.movieOnline = res.data;
                        }
                    });
                //电影票房排行
                api.starDetailsMovieBoxOffice(this.$route.params.id)
                    .then(res=>{
                        if(res.data.length != 0){
                            let maxData = res.data[0].domesticboxoffice + res.data[0].foreignboxoffice;
                            maxData +=1000
                            res.data.forEach(function (e) {
                                e.maxdata = maxData
                            });
                            this.movieBoxOffice=res.data;
                        }
                    });
                //电影在线播放总量
                api.starDetailsMovieOnlineMovie(this.$route.params.id)
                    .then(res=>{
                        if(res.data.length != 0){
                            let maxData = res.data[0].onlineplayback;
                            res.data.forEach(function (e) {
                                e.maxdata = maxData
                            });
                            this.movieOnlineMovie = res.data;
                        }
                    });
                //初始化豆瓣评分列表
                api.starDetailsValveScore({
                    "starid":this.$route.params.id,
                    "page":1,
                    "rows":6
                })
                    .then(res=>{
                        for(let i = 0;i < res.data.length;i++){
                            res.data[i].doubanscore=res.data[i].doubanscore ? res.data[i].doubanscore.toFixed(1) : ''
                        }
                        this.movieList = res.data;
                        this.movielistlen = res.count;
                    })
            },
            //点击豆瓣评分按钮渲染数据
            movieListBtn(pageIndex){
                let btnDiv = document.getElementById('movieListBtn');
                let btnSpan = btnDiv.getElementsByTagName('span');
                let btnSpanLen = btnSpan.length;
                for(let i = 0;i < btnSpanLen;i++){
                    btnSpan[i].className='';
                }
                btnSpan[pageIndex - 1].className='active';
                api.starDetailsValveScore({
                        "starid":this.$route.params.id,
                        "page":pageIndex,
                        "rows":6
                    })
                    .then(res=>{
                        for(let i = 0;i < res.data.length;i++){
                            if(res.data[i].doubanscore && res.data[i].doubanscore!= ''){
                                res.data[i].doubanscore=res.data[i].doubanscore.toFixed(1)
                            }
                        }
                        this.movieList = res.data;
                    })
            }
        },
        computed:{
            //按钮个数
            itemClass(){
                let totalPage = Math.ceil(this.movielistlen / 6);
                return totalPage;
            },
            ...mapGetters([
                'user'
            ])
        },
        components:{
            BarChart,
            BarOnlinePlay,
            BarBoxOffice,
            star
        },
        watch:{
            'user.useId'(val,oldval){
                if(val){
                    this.cookie =  cookie.get("useId")
                    this.loadmovieinfo()
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .movies-list{
        width: 1200px;
        margin:0 auto;
        padding-top: 25px;
    }
    .title{
        margin-bottom:20px;
        padding-left:40px;
        text-align:left;
        font-size:18px;
        color:#666;
        background:url(../../../static/images/TVList.png) no-repeat 10px center;
    }
    .left-legend{
        margin-right:40px;
        font-size:14px;
        color:#666;
    }
    .left-legend i{
        float:left;
        width:12px;
        height:8px;
        margin:3px 6px 0 0;
        border-radius:2px;
        background: #eca6fa;
        background: -webkit-linear-gradient(to right, #eca6fa, #c370f8);
        background: -moz-linear-gradient(to right, #eca6fa, #c370f8);
        background: -ms-linear-gradient(to right, #eca6fa, #c370f8);
        background: linear-gradient(to right, #eca6fa, #c370f8);
    }
    .right-legend{
        margin-right:0;
    }
    .right-legend i{
        background: #13cafe;
        background: -webkit-linear-gradient(to right, #13cafe, #76a3fd);
        background: -moz-linear-gradient(to right, #13cafe, #76a3fd);
        background: -ms-linear-gradient(to right, #13cafe, #76a3fd);
        background: linear-gradient(to right, #13cafe, #76a3fd);
    }
    .left-charts-wrapper{
        width:595px;
        background:#fff;
        border-radius:8px;
        box-shadow: 0 0 10px 1px #dfeafb;
    }
    .left-charts-wrapper li{
        /*box-sizing: content-box;*/
        height: 97px;
        padding: 16px 26px 21px;
    }
    li.nodata{
        line-height: 60px;
        font-size: 18px;
        color: #666;
        text-align: center;
    }
    .nomovie{
        width: 100%;
        height: 60px;
        line-height: 60px;
        text-align: center;
        font-size: 18px;
        color: #666;
        margin-bottom: 20px;
    }
    .left-charts-wrapper li:hover{
        background-color: #eff1fe;
    }
    .left-charts-wrapper li:hover .charts-name{
        color: #9e5bfc;
    }
    .left-charts-con{
        /*padding:26px;*/
        padding:26px 0;
    }
    .titleline{
        padding: 0 26px;
    }
    .chart-title{
        padding-left:13px;
        text-align:left;
        font-size:16px;
        color:#666;
        background:url(../../../static/images/titleIco.png) no-repeat left center;
    }
    .bottom-left-charts{
        margin-top:20px;
    }
    .bottom-left-charts img{
        object-fit: cover;
    }
    .bottom-left-charts li{
        width:100%;
        /*height:60px;*/
        /*margin:16px 0 21px 0;*/
    }
    .bottom-left-charts li:hover .charts-img-wrapper,
    .bottom-left-charts li.active .charts-img-wrapper{
        border:2px solid #b35bf8;
    }
    /*.bottom-left-charts li:hover .charts-num span,*/
    .bottom-left-charts li.active .charts-num span{
        background:#ffcc00;
        border:1px solid #ffff0e;
        border-radius:50%;
        box-shadow: 0 3px 10px 2px #dfeafb;
        color:#fff;
    }
    .charts-img-wrapper{
        overflow:hidden;
        width:60px;
        height:60px;
        border:2px solid #d8acfb;
        border-radius:5px;
    }
    .charts-img-wrapper img{
        width:100%;
        height:100%;
    }
    .charts-num span{
        float:right;
        width:31px;
        height:31px;
        background:transparent;
        border:1px solid transparent;
        font-size:20px;
        color:#999;
        line-height:31px;
    }
    .charts-num span.left-top-box-office{
        width: 24px;
        height: 24px;
        margin-right: 0;
        background: #c9c9c9;
        border: 1px solid #ffff07;
        border-radius: 50%;
        font-size: 14px;
        line-height: 24px;
        text-align: center;
        color: #fff;
        box-shadow: none !important;
    }
    .charts-name{
        font-size:14px;
        color:#666;
        line-height:30px;
    }
    .chart{
        /*width:320px;*/
        width:350px;
        height:60px;
    }
    .chart-charts1 .charts-name{
        margin-right:10px;
    }
    .tvname{
        text-align: left;
        font-size: 14px;
        color: #666;
    }
    .chart1{
        width:340px;
        height:40px;
    }
    .bottom-left-charts1{
        margin-top:20px;
    }
    .bottom-left-charts1 li{
        /*margin:14px 0;*/
        /*height:40px;*/
    }
    .chart-charts1{
       margin-top:3px;
    }
    .movie-charts-wrapper{
        margin-top:50px;
    }
    .title1{
        background:url(../../../static/images/MVList.png) no-repeat 10px center;
    }
    .box-office{
        width:473px;
        /*margin-left:10px;*/
        padding-left: 10px;
    }
    .top-box-office{
        font-size:14px;
        color:#666;
        line-height:24px;
        text-align:left;
    }
    .left-top-box-office{
        width:24px;
        height:24px;
        margin-right:10px;
        background:#c9c9c9;
        border:1px solid #ffff07;
        border-radius:50%;
        font-size:14px;
        line-height:24px;
        text-align:center;
        color:#fff;
    }
    .bottom-box-office{
        margin-top:10px;
        height:24px;
        line-height:24px;
        color:#666;
        font-size:14px;
    }
    .chart3{
        width:340px;
        height:100%;
    }
    /*.bottom-left-charts2 li:hover .left-top-box-office,*/
    .bottom-left-charts2 li.active .left-top-box-office{
        background:#ffcc00;
    }
    .bottom-box-office p{
        text-align:right;
    }
    .box-office1{
        width:100%;
    }

    .title2{
        background:url(../../../static/images/score.png) no-repeat 10px center;
    }
    .movie-list-wrapper{
        width:1200px;
        /*height:355px;*/
        overflow:hidden;
    }
    .movie-list-con{
        width:1200px;
        margin:20px 0 30px 0;
        height:320px;
    }
    .movie-list-con li{
        float:left;
        width:184px;
        height:307px;
        margin:0 8px 0 8px;
        background:#fff;
        box-shadow: 0 3px 6px 1px #dfeafb;
        border-radius:8px;
        overflow:hidden;
        transition:all ease .3s;
    }
    .movie-list-con li:hover,.movie-list-con li.active{
        transform:scale(1.1);
        border-radius:10px;
    }

    .movie-list-con li img{
        width:100%;
        height:220px;
    }
    .movie-name{
        font-size:14px;
        color:#666;
        line-height:38px;
    }
    .movie-score{
        padding: 0 25px;
    }
    .movie-score-num{
        /*margin-left:28px;*/
        font-size:32px;
        color: #c953ff;
        font-weight:bold;
        background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(rgba(2510, 162, 254, 1)), to(rgba(181, 51, 255, 1)));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    .movie-score-con{
        /*margin-left:15px;*/
    }
    .movie-score-star{
        height:14px;
    }
    .movie-score-people{
        line-height:20px;
        font-size:12px;
        color:#269ef5;
    }
    .movie-list-btn{
        height:24px;
        text-align:center;
        margin:20px 0 50px 0;
    }
    .movie-list-btn span{
        display:inline-block;
        width:16px;
        height:16px;
        margin-right:7px;
        border:2px solid #fff;
        border-radius:50%;
        background:#d2d2d2;
        box-shadow: 0 3px 6px 1px #dfeafb;
        cursor:pointer;
    }
    .movie-list-btn span:hover,.movie-list-btn span.active{
        transform:scale(1.3);
        background:#d487fa;
    }
</style>



