<template>
    <div class="star-details">
        <div class="details">
            <div class="con">
                <p class="backLine"><span class="starlist" @click="tolist">全部明星</span>&nbsp;&gt;&nbsp;<span class="current">明星详情</span></p>
                <p class="nameLine">
                    <span class="starName">{{start.name}}</span>
                    <span class="index">口碑蹿升指数：<i class="indexNum">{{start.jumpupindex}}</i><span class="source">（{{start.jumpupworks}}）</span></span>
                </p>
            </div>
            <div class="detailsList cf">
                <div class="listcon">
                    <div class="commercialCon fl">
                        <p class="commercial">商业价值指数</p>
                        <p class="commercialNum">{{start.businessvalueindex}}</p>
                    </div>
                    <div class="starInfo fl">
                        <ul class="cf">
                            <li class="cf"><span class="label">籍贯：</span><span class="info" :title=start.birthplace >{{start.birthplace}}</span></li>
                            <li class="cf"><span class="label">出生日期：</span><span class="info">{{start.birthdate}}</span></li>
                            <li class="cf"><span class="label">身高：</span><span class="info">{{start.height}}</span></li>
                            <li class="cf"><span class="label">体重：</span><span class="info">{{start.weight}}</span></li>
                            <li class="cf"><span class="label">职业：</span><span class="info" :title=start.profession  >{{start.profession}}</span></li>
                            <li class="cf"><span class="label">活跃领域：</span><span class="info" :title=start.activefield  >{{start.activefield}}</span></li>
                            <li class="cf"><span class="label">特长：</span><span class="info" :title=start.specialty  >{{start.specialty}}</span></li>
                            <li class="cf"><span class="label">毕业院校：</span><span class="info" :title=start.graduatedschool  >{{start.graduatedschool}}</span></li>
                            <li class="bottom-line cf"><span class="label">经济公司：</span><span class="info" :title=start.agency  >{{start.agency}}</span></li>
                            <li class="bottom-line cf">
                                <span class="label">获得奖项：</span>
                                <span class="info">
                                    <span class="awardslist" :title=start.getreward>
                                        {{start.getreward1}}
                                    </span>
                                </span>
                            </li>
                        </ul>
                    </div>
                    <div class="imgcon fr">
                        <img :src = imgurl  alt="明星写真">
                    </div>
                </div>
            </div>
        </div>
        <nav>
            <router-link :class="['width'+labelcount,'movie']" :to="moviepath" v-if="movieflag">影视</router-link>
            <router-link :class="['width'+labelcount,'music']" :to="musicpath" v-if="musicflag">音乐</router-link>
            <router-link :class="['width'+labelcount,'shows']" :to="showspath" v-if="showflag">演出</router-link>
            <router-link :class="['width'+labelcount,'internet']" :to="internetpath" v-if="netflag">互联网</router-link>
        </nav>
        <router-view></router-view>
        <!--<div class="no-power" v-if="!this.user.useId && !userId">-->
            <!--<tologin @showlogin="ishowlogin"></tologin>-->
        <!--</div>-->
        <!--<login-view v-if="loginflag" :activeflag="true" @getcookie="getcookie" @hide="tohide"></login-view>-->
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import baseconfig from '@/assets/config'
    import loginView from '@/components/loginView/index.vue'
    import tologin from '@/components/loginView/tologin.vue'
    import api from '@/fetch/api'
    import cookie from '@/util/cookie'
    export default {
        name: 'details',
        mounted () {
            if(!this.loginflag){document.body.className = ''}
            this.moviepath=`/details/${this.$route.params.id}/movies`
            this.musicpath=`/details/${this.$route.params.id}/music`
            this.showspath=`/details/${this.$route.params.id}/shows`
            this.internetpath=`/details/${this.$route.params.id}/internet`
            this.userId= cookie.get("useId")
            api.starDetails(this.$route.params.id)
                .then(res=>{
                    if(res.result == 1){
                        this.movieflag = res.data[1].movieandtv ==1 ? true :false
                        this.musicflag = res.data[1].music ==1 ? true :false
                        this.showflag = res.data[1].performance ==1 ? true :false
                        this.netflag = res.data[1].publicopinion ==1 ? true :false

                        this.start=res.data[0]
                        if(this.start.getreward.length > 52){
                            this.start.getreward1 = this.start.getreward.substring(0,40) + '...'
                        } else {
                            this.start.getreward1 = this.start.getreward
                        }

//                        this.imgurl = baseconfig.baseURL.substring(0,baseconfig.baseURL.length - 1) + this.start.starimg
                        this.imgurl = this.start.starimg
                        this.chagenavwidth()
                        this.selfirst()
                        this.getcookie()
                    }
                })
        },
        data () {
            return {
                current:0,
                loginflag: false,
                imgurl:'',
                firstlabel: '',
                movieflag: '',
                musicflag: '',
                showflag: '',
                netflag:'',
                labelcount: 1,
                moviepath:'',
                musicpath:'',
                showspath:'',
                internetpath:'',
                userId:'',
                start:{}
            }
        },
        methods:{
            ishowlogin(){
                if(!this.userId){
                    this.loginflag = true
                    document.body.scrollTop = 0
                    document.documentElement.scrollTop = 0
                    document.body.className = 'dislogin'
//                    document.body.style.overflow='hidden'
                }
            },
            tohide(){
                this.loginflag = false
                document.body.className = ''
            },
            getcookie(){
                this.userId= cookie.get("useId")
                if(this.userId){
                    this.loginflag = false
                    if(this.$route.name){
                        if( this.$route.name != 'details'){
                            this.$router.push(`/details/${this.$route.params.id}/`+ this.$route.name)
                        } else {
                            this.$router.push(`/details/${this.$route.params.id}/`+this.firstlabel)
                        }
                    }
                }
            },
            tolist(){
                this.$router.push({ name: 'list'})
            },
            chagenavwidth(){
                let n=1, arrflag=[this.movieflag,this.musicflag,this.showflag]
                arrflag.forEach(function (item) {
                    if(item) n++
                })
                return this.labelcount = n
            },
            selfirst(){
                return this.firstlabel = this.movieflag ? 'movies' : (this.musicflag ? 'music' : (this.showflag ? 'shows' : ( this.netflag ? 'internet': '') ))
            }
        },
        computed:{
            ...mapGetters([
                'user'
            ])
        },
        components:{
            loginView,
            tologin
        },
        watch:{
            'user.useId'(val,oldval){
                if(val){
                    this.getcookie()
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .star-details{
        min-height: calc(100vh - 157px);
        /*background-color: #f2f7ff;*/
        background-color: transparent;
        overflow: hidden;
    }
    .details{
        position: relative;
        width: 100%;
        height: 380px;
        /*background: #e7d8f9 url('../../../static/images/body-bg.png') center no-repeat;*/
    }
    .details .con{
        width: 1200px;
        margin: 0 auto;
        height: 100%;
        padding-top: 30px;
    }
    .details .con .backLine{
        margin-bottom: 34px;
        text-align: left;
        color: #664ab8;
        font-size: 14px;
        cursor: pointer;
    }
    .details .con .backLine .current{
        color: #9c55fd;
        font-size: 16px;
        cursor: default;
    }
    .details .con .nameLine{
        padding-left: 25px;
        margin-bottom: 46px;
        text-align: left;
    }
    .details .con .nameLine .starName{
        color: #a961fe;
        font-size: 52px;
        font-weight: bold;
    }
    .details .con .nameLine .index{
        color: #666;
        font-size: 20px;
        margin-left: 89px;
    }
    .details .con .nameLine .indexNum{
        font-size: 44px;
        font-weight: bold;
        color: #a961fe;
        margin: 0 15px;
    }
    .details .con .nameLine .source{
        font-size: 14px;
    }
    .details .detailsList{
        position: absolute;
        width: 100%;
        /*top: 172px;*/
        top: 193px;
        padding: 10px 0;
        background: url("../../../static/images/infoBg.png") center no-repeat;
    }
    .details .detailsList .listcon{
        position: relative;
        width: 1200px;
        height: 300px;
        margin: 0 auto;
        border-radius: 7px;
    }
    .details .detailsList .commercialCon{
        width: 140px;
        margin-left: 30px;
        color: #fff;
        /*padding: 50px 30px;*/
        padding: 50px 0;
    }
    .details .detailsList .commercialCon p{
        width: 100%;
        text-align: center;
    }
    .details .detailsList .commercial{
        font-size: 20px;
        font-weight: 600;
        line-height: 30px;
        margin-bottom: 90px;
        padding: 0 30px;
    }
    .details .detailsList .commercialNum{
        /*font-size: 50px;*/
        font-size: 40px;
        font-weight: bold;
        padding: 0 10px;
    }
    .details .detailsList .starInfo{
        /*width: 670px;*/
        width: 594px;
        height: 100%;
        margin-left: 36px;
        padding-top: 80px;
    }
    .details .detailsList .starInfo ul{
        width: 100%;
    }
    .details .detailsList .starInfo li{
        float: left;
        width: 50%;
        margin-bottom: 20px;
    }
    .details .detailsList .starInfo .label,
    .details .detailsList .starInfo .info{
        float: left;
        display: inline-block;
        text-align: left;
        color: #fff;
        font-size: 14px;
    }
    .details .detailsList .starInfo .label{
        /*margin-right: 30px;*/
        margin-right: 15px;
        width: 70px;
    }
    .details .detailsList .starInfo .info{
        width: calc(100% - 100px);
        height: 14px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .detailsList .starInfo .bottom-line .info{
        height: auto;
        white-space: normal;
        max-height: 65px;
    }
    .details .detailsList .starInfo .awardslist{
        margin-top: -2px;
        display: inline-block;
        width: 100%;
        /*height: 65px;*/
        line-height: 22px;
    }
    .details .detailsList .starInfo .awardslist li{
        width: 100%;
        margin-bottom: 8px;
        text-align: left;
        color: #fff;
        font-size: 14px;
    }
    .details .detailsList .imgcon{
        position: relative;
        /*width: 275px;*/
        width: 400px;
        height: 100%;
    }
    .details .detailsList .imgcon img{
        position: absolute;
        right: 0;
        bottom: 0;
        width: 100%;
        /*height: 465px;*/
        height: 503px;
        border-bottom-right-radius: 7px;
        overflow: hidden;
    }

    .aaa.aa1{

    }

    nav{
        /*display: inline-block;*/
        width: 1200px;
        border-radius: 8px;
        margin: 0 auto;
        /*margin-top: 160px;*/
        margin-top: 180px;
        box-shadow: 0 0 10px 1px #dfeafb;
        margin-bottom: 28px;
        overflow: hidden;
    }
    nav a{
        float:left;
        display: inline-block;
        background: #f7faff;
        height: 60px;
        line-height: 60px;
        font-size: 16px;
        color: #666;
        position: relative;
        background-position: 38% center;
        background-repeat: no-repeat;
    }
    nav a:hover{
        background-color: #eff1fe;
    }
    nav a.router-link-active{
        background-color: #fff;
        /*color: #a66af9;*/
        color: #9e5bfc;
        font-size: 20px;
        cursor: default;
    }
    .width1{
        width: 100%;
        background-position: 46% center;
    }
    .width2{
        width: calc(100% / 2);
        background-position: 42% center;
    }
    .width3{
        width: calc(100% / 3);
    }
    .width4{
        width: calc(100% / 4);
    }
    nav a.movie{
        background-image: url(../../../static/images/movies－nl.png);
    }
    nav a.movie:hover{
        background-image: url(../../../static/images/movies－hr.png);
    }
    nav a.router-link-active.movie{
        background-image: url(../../../static/images/movies－ck.png);
    }
    nav a.music{
        background-image: url(../../../static/images/music－nl.png);
    }
    nav a.music:hover{
        background-image: url(../../../static/images/music－hr.png);
    }
    nav a.router-link-active.music{
        background-image: url(../../../static/images/music－ck.png);
    }
    nav a.shows{
        background-image: url(../../../static/images/show－nl.png);
    }
    nav a.shows:hover{
        background-image: url(../../../static/images/show－hr.png);
    }
    nav a.router-link-active.shows{
        background-image: url(../../../static/images/show－ck.png);
    }
    nav a.internet{
        padding-left: 14px;
        background-image: url(../../../static/images/internet－nl.png);
    }
    nav a.internet:hover{
        background-image: url(../../../static/images/internet－hr.png);
    }
    nav a.router-link-active.internet{
        padding-left: 40px;
        background-image: url(../../../static/images/internet－ck.png);
    }
    nav a.movie:after{
        position: absolute;
        right: 0;
        top:0;
        content: '';
        display: inline-block;
        width: 1px;
        height: 100%;
        /*background-image:linear-gradient(0deg, #f7faff, #f7faff, #929deb, #929deb, #f7faff,#f7faff);*/
        background: url("../../../static/images/slide.png") center no-repeat;
    }
    nav a.music:after{
        position: absolute;
        right: 0;
        top:0;
        content: '';
        display: inline-block;
        width: 1px;
        height: 100%;
        /*background-image:linear-gradient(0deg, #f7faff, #f7faff, #929deb, #929deb, #f7faff,#f7faff);*/
        background: url("../../../static/images/slide.png") center no-repeat;
    }
    nav a.shows:after{
        position: absolute;
        right: 0;
        top:0;
        content: '';
        display: inline-block;
        width: 1px;
        height: 100%;
        /*background-image:linear-gradient(0deg, #f7faff, #f7faff, #929deb, #929deb, #f7faff,#f7faff);*/
        background: url("../../../static/images/slide.png") center no-repeat;
    }
</style>



