import Vue from 'vue'
import Router from 'vue-router'
import List from '@/pages/List/Index.vue'
import details from '@/pages/details/index.vue'
import movies from '@/pages/details/movies.vue'
import music from '@/pages/details/music.vue'
import shows from '@/pages/details/shows.vue'
import internet from '@/pages/details/internet.vue'
import retrievePassword from '@/pages/retrievePassword/index.vue'
import retrievePassword1 from '@/pages/retrievePassword/retrievePassword1.vue'
import retrievePassword2 from '@/pages/retrievePassword/retrievePassword2.vue'
import retrievePassword3 from '@/pages/retrievePassword/retrievePassword3.vue'

Vue.use(Router)

const router=new Router({
    routes: [
        {
            path: '/',
            name: 'list',
            component: List
        },
        {
            path: '/details/:id',
            name: 'details',
            component: details,
            children:[
                {
                    path: '/details/:id/movies',
                    name: 'movies',
                    component: movies
                },
                {
                    path: '/details/:id/music',
                    name: 'music',
                    component: music
                },
                {
                    path: '/details/:id/shows',
                    name: 'shows',
                    component: shows
                },
                {
                    path: '/details/:id/internet',
                    name: 'internet',
                    component: internet
                }
            ]
        },
        {
            path: '/retrievePassword',
            name: 'retrievePassword',
            component: retrievePassword,
            children:[
                {
                    path:'retrievePassword1',
                    name:'retrievePassword1',
                    component:retrievePassword1
                },
                {
                    path: 'retrievePassword2/:phonenmber',
                    name: 'retrievePassword2',
                    component: retrievePassword2
                },
                {
                    path: 'retrievePassword3',
                    name: 'retrievePassword3',
                    component: retrievePassword3
                },
                {
                    path: '',
                    redirect:'retrievePassword1'
                }
            ]
        }
    ]
})

router.beforeEach((to, from, next) => {
    let t =/^[0-9]*[1-9][0-9]*$/
    // console.log(to.path.substring(to.path.indexOf('/') + 1,to.path.lastIndexOf('/')))
    if( t.test(to.path.substring(to.path.lastIndexOf('/')+1)) && (to.path.substring(to.path.indexOf('/')+1,to.path.lastIndexOf('/')) == 'details') ){
        // next('/list')
        next({ name: 'list'})
    }
    next()
})

export default router



