<template>
    <div class="progress">
        <div class="progressBar progressBar3"></div>
        <ul class="progressText">
            <li class="active">输入手机号</li>
            <li class="active">输入新密码</li>
            <li class="active">重置成功</li>
        </ul>
        <div class="retrievePassword3Wrapper">
            <p class="retrievePassword3WrapperT"><i></i><span>您的密码已经修改成功！</span></p>
            <p class="retrievePassword3WrapperB">{{num}}秒钟后自动跳转，或<router-link to="/">点击进入主页&nbsp;&gt;&nbsp;</router-link></p>
        </div>

    </div>
</template>

<script>
    import {mapGetters} from 'vuex'

    export default{
        name: 'retrievePassword2',
        data(){
            return{
                num:5
            }
        },
        created () {
            this.toLogin();
        },
        methods:{
            toLogin(){
               let timer=setInterval(()=>{
                    this.num-=1
                    if (this.num==0) {
                        clearInterval(timer)
                        this.$router.push({name: 'list'})
                    }
                },1000)
            }
        }
    }
</script>
<style>
    .retrievePassword3Wrapper{
        width:300px;
        margin:120px auto 0;
    }
    .retrievePassword3WrapperT{
        height:36px;
        line-height:36px;
        color:#666;
    }
    .retrievePassword3WrapperT i{
        width:36px;
        height:36px;
        float:left;
        margin-left:20px;
        background:url(../../../static/images/success.png) no-repeat;
    }
    .retrievePassword3WrapperT span{
        float:left;
        margin-left:5px;
        font-size:20px;
    }
    .retrievePassword3WrapperB{
        margin-top:45px;
        font-size:16px;
        color:#666;
    }
    .retrievePassword3WrapperB a{
        color:#0078ff;
    }
</style>




